import React, { useRef, useEffect, useState } from 'react';

const CanvasDoodle = ({ zIndex = "1", width, height, angle }) => {
    const canvasRef = useRef(null);
    const [drawing, setDrawing] = useState(false);

    function getRotatedCoordinates(canvas, x, y, angle) {

    
        const centerX = canvas.width / 2;
        const centerY = canvas.height / 2;
        const radians = (angle * Math.PI) / 180; // Convert degrees to radians
    
        // Translate the coordinates to the center of the canvas
        const dx = (x - centerX);
        const dy = (y - centerY);
    
        // Apply the rotation transformation
        const rotatedX = centerX + dx * Math.cos(radians) - dy * Math.sin(radians);
        const rotatedY = centerY + dx * Math.sin(radians) + dy * Math.cos(radians);
    
        return { x: rotatedX, y: rotatedY };
    }
    useEffect(() => {
        const canvas = canvasRef.current;
        const ctx = canvas.getContext('2d');

        const startDrawing = (event) => {
            setDrawing(true);
            draw(event); // Draw a point where the mouse is clicked
        };

        const stopDrawing = () => {
            setDrawing(false);
            ctx.beginPath(); // Reset the path to avoid connecting lines
        };

        const draw = (event) => {
           
            if (!drawing) return;

            ctx.lineWidth = 3;
            ctx.lineCap = 'round';
            ctx.strokeStyle = 'black';

            var rect = event.target.getBoundingClientRect();
            var _x = event.clientX - rect.left; //x position within the element.
            var _y = event.clientY - rect.top;  //y position within the element.
            console.log("Left? : " + _x + " ; Top? : " + _y + ".");

            const { x, y } = getRotatedCoordinates(canvas, _x, _y, angle)
            ctx.lineTo(x, y);
            ctx.stroke();
            ctx.beginPath();
            ctx.moveTo(x, y);
        };

        canvas.addEventListener('mousedown', startDrawing);
        canvas.addEventListener('mouseup', stopDrawing);
        canvas.addEventListener('mousemove', draw);

        return () => {
            canvas.removeEventListener('mousedown', startDrawing);
            canvas.removeEventListener('mouseup', stopDrawing);
            canvas.removeEventListener('mousemove', draw);
        };
    }, [drawing]);

    return (
        <div
            style={{
                transformOrigin: "center",
                position: "absolute",
                zIndex: 10000000011, 
                backgroundColor: 'lightblue',
                transform: "translate(-50%, -50%)"
            }}
        >
        <canvas
            ref={canvasRef}
            height={height}
            width={width}
            style={{
                border: "1px solid black",
                cursor: 'url(images/pencil-cursor.png)', 
                
                position: 'absolute'
            }}
        ></canvas>
        </div>
    );
};

export default CanvasDoodle;
